.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		padding: 0 0 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 0 0 5rem;
		@include max-md {
			padding: 0 0 3rem;
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		text-align: left;
		background-color: var(--color-primary);
		color: var(--color-light);
		padding: 5rem 0 10rem;
		@include max-md {
			padding: 3rem 0 8rem;
		}
		.title-section {
			text-align: left;
		}
	}
	.couple {
		position: relative;
		text-align: center;
		padding: 1px;
		margin-top: -7rem;
		@include max-md {
			margin-top: -5rem;
		}
		&.woman {
			margin-top: 0;
		}
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 7/9;
			margin: 0 auto;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: var(--border-radius);
					border: 1px solid $stroke-color;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem;
			h3 {
				font-weight: 400;
			}
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--text-button);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
