.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.rsvp-wrapper {
		position: relative;
		padding: 8rem 3rem;
		color: var(--color-light);
		@include max-lg {
			padding: 7rem 2rem;
		}
		.elements-wrapper-absolute {
			.background-element {
				img {
					border-radius: var(--border-radius);
					border: 1px solid $stroke-color;
				}
			}
		}
		.rsvp-form {
			position: relative;
			.title-section {
				&::before {
					all: unset;
				}
			}
		}
	}
}
